import constVal from '../../unit/const.js';
import Block from '../../unit/block.js';

let {
	lastRecord
} = constVal;

const initState = (() => {
	if (!lastRecord || !lastRecord.cur) { // 无记录 或 有记录 但方块为空, 返回 null
		return null;
	}
	const cur = lastRecord.cur;
	const option = {
		type: cur.type,
		rotateIndex: cur.rotateIndex,
		shape: cur.shape.map(e => e.slice()),
		xy: cur.xy,
	};
	return new Block(option);
})();

export default {
	state: initState,
	moveBlock(state, payload) {
		state.cur = payload;
	}
}
