import constVal from '../../unit/const.js';
let {
	lastRecord
} = constVal;

let initState = lastRecord && !isNaN(parseInt(lastRecord.clearLines, 10)) ?
	parseInt(lastRecord.clearLines, 10) : 0;
if (initState < 0) {
	initState = 0;
}

export default {
	state: initState,
	clearLines(state, payload) {
		state.clearLines = payload;
	}
};
