import constVal from '../../unit/const.js';

let {
	lastRecord
} = constVal;

let initState = lastRecord && !isNaN(parseInt(lastRecord.startLines, 10)) ?
	parseInt(lastRecord.startLines, 10) : 0;
if (initState < 0 || initState > 10) {
	initState = 0;
}

export default {
	state: initState,
	startLines(state, payload) {
		state.startLines = payload; // 最大分数
	}
};