export default {
	state: {
		drop: false,
		down: false,
		left: false,
		right: false,
		rotate: false,
		reset: false,
		music: false,
		pause: false
	},
	mutations: {
		drop(state, payload) {
			state.drop = payload;
		},
		down(state, payload) {
			console.log("down", payload)
			state.down = payload;
		},
		left(state, payload) {
			state.left = payload;
		},
		right(state, payload) {
			state.right = payload;
		},
		rotate(state, payload) {
			state.rotate = payload;
		},
		"key-reset": function(state, payload) {
			state.reset = payload;
		},
		"key-music": function(state, payload) {
			state.music = payload;
		},
		"key-pause": function(state, payload) {
			state.pause = payload;
		},
	}
};