<template>
	<div class="bg music" :class="{c:!music}"></div>
</template>

<script>
	import {
		mapState
	} from 'vuex';
	export default {
		name: 'Music',
		computed: {
			...mapState({
				music: (state) => {
					// console.log("music", state.music)
					return state.music;
				},
			})
		}
	}
</script>

<style scoped lang="less">
	.music {
		width: 25px;
		height: 21px;
		background-position: -175px -75px;
		position: absolute;
		top: 2px;
		left: -12px;

		&.c {
			background-position: -150px -75px;
		}
	}
</style>
