import constVal from '../../unit/const.js';

let {
	lastRecord
} = constVal;

let initState = lastRecord && !isNaN(parseInt(lastRecord.speedStart, 10)) ?
	parseInt(lastRecord.speedStart, 10) : 1;
if (initState < 1 || initState > 6) {
	initState = 1;
}

export default {
	state: initState,
	speedStart(state, payload) {
		state.speedStart = payload; // 最大分数
	}
};