import index from '../../unit/index.js';
import constVal from '../../unit/const.js';

let {
	getNextType
} = index;
let {
	lastRecord,
	blockType
} = constVal;

const initState = lastRecord && blockType.indexOf(lastRecord.next) !== -1 ?
	lastRecord.next : getNextType();

export default {
	state: initState,
	next(state, payload) {
		state.next = payload;
	}
};