import constVal from '../../unit/const.js';

let {
	blankMatrix,
	lastRecord
} = constVal;

const initState = lastRecord && Array.isArray(lastRecord.matrix) ?
	lastRecord.matrix.map(e => e.slice()) : blankMatrix;

export default {
	state: initState,
	matrix(state, payload) {
		// console.log("触发了matrix", payload)
		state.matrix = payload;
	}

};
