<template>
	<div class="matrix">
		<p v-for="(p,k1) in matrix2" :key="k1">
			<b v-for="(e,k2) in p" :key="k2" :class="{c:e===1,d:e===2,h:e===3}"></b>
		</p>
	</div>
</template>

<script>
	let log = function() {
		// console.log("Matrix:", ...arguments)
	}

	import {
		mapState
	} from 'vuex';
	import index from '../unit/index.js';
	import constObj from '../unit/const.js';
	import states from '../control/states.js';
	import keyboard from '../actions/keyboard.js';
	let {
		isClear
	} = index;
	let {
		fillLine,
		blankLine
	} = constObj;

	const t = setTimeout;

	export default {
		name: 'Matrix',
		props: {
			cur: {
				type: Object
			},
		},
		data() {
			return {
				clearLines: false,
				animateColor: 2,
				overState: null,
				matrix2: [],
			};
		},
		created() {
			this.matrix2 = this.cloneMatrix(this.matrix);
		},
		computed: {
			...mapState({
				matrix: (state) => {
					return state.matrix;
				},
				reset: (state) => {
					log("reset", state.reset)
					return state.reset;
				},
				help: (state) => {
					return state.help;
				}
				// cur: (state) => {
				// 	return state.cur;
				// },
			})
		},
		watch: {
			reset(newVal, oldVal) {
				//重置时开启动画
				if (newVal) {
					this.over();
				}
			},
			//看一下变化了就要去将行清除
			matrix(newVal, oldVal) {
				const clears = isClear(newVal);
				//有要清除的行并且不是在重置的时候
				if (clears && !this.reset) {
					this.clearAnimate(clears);
				}
			},
			cur(newVal, oldVal) {
				// console.log("auto cur", newVal)
				this.matrix2 = this.cloneMatrix(this.getResult());
			},
		},
		methods: {
			cloneMatrix(matrix) {
				matrix = matrix || [];
				return matrix.map((m) => {
					return m.slice();
				})
			},
			clearAnimate(clears) {
				const anima = (callback) => {
					t(() => {
						this.animateColor = 0;
						this.matrix2 = this.cloneMatrix(this.getResult());
						t(() => {
							this.animateColor = 2;
							this.matrix2 = this.cloneMatrix(this.getResult());
							if (typeof callback === 'function') {
								callback();
							}
						}, 100);
					}, 100);
				};
				// return states.clearLines(this.matrix, clears);
				anima(() => {
					anima(() => {
						anima(() => {
							t(() => {
								states.clearLines(this.matrix, clears);
							}, 100);
						});
					});
				});
			},
			//开头的动画
			over() {
				// let overState = this.getResult();
				let overState = this.cloneMatrix(this.getResult());

				const exLine = (index) => {
					if (index <= 19) {
						overState.splice(19 - index, 1, fillLine.slice());
					} else if (index >= 20 && index <= 39) {
						overState.splice(index - 20, 1, blankLine.slice());
					} else {
						// console.log("auto end")
						states.overEnd();
						return;
					}
					this.matrix2 = overState;
				};

				for (let i = 0; i <= 40; i++) {
					t(exLine.bind(null, i), 40 * (i + 1));
				}

				this.matrix2 = overState;
			},
			clearLineColor(clearLines, matrix) {
				const animateColor = this.animateColor;
				clearLines.forEach((index) => {
					let a = [animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
						animateColor,
					];
					matrix.splice(index, 1, a);
				});
				return matrix;
			},
			getResult() {
				const cur = this.cur;
				const shape = cur && cur.shape;
				const xy = cur && cur.xy;
				let matrix = this.cloneMatrix(this.matrix);
				const clearLines = isClear(this.matrix);
				if (clearLines) {
					return this.clearLineColor(clearLines, matrix);
				} else if (shape) {
					if (this.help) {
						let shapeWidth = shape[0].length;
						let shapeHeight = shape.length;
						let x = xy[0];
						let color = 3;
						let $xy = xy.slice();
						for (let k = x + 1; k <= (20 - shapeHeight); k++) {
							let _xy = [k, xy[1]];
							let success = true;
							for (let k1 = shapeHeight - 1; k1 >= 0; k1--) {
								let m = shape[k1]; //形状的最后一行
								m.forEach((n, k2) => {
									let line = matrix[_xy[0] + k1];
									if (n == 1 && line[_xy[1] + k2] == 1) {
										success = false;
									}
								});
							}
							if (!success) {
								$xy[0] = k - 1;
								break;
							} else {
								$xy[0] = k;
							}
						}
						if ($xy.toString() !== xy.toString()) {
							shape.forEach((m, k1) => (
								m.forEach((n, k2) => {
									let line = matrix[$xy[0] + k1];
									if (n == 1) {
										line[$xy[1] + k2] = 3;
									}
								})
							));
						}
					}
					shape.forEach((m, k1) => (
						m.forEach((n, k2) => {
							if (n && xy[0] + k1 >= 0) { // 竖坐标可以为负
								let line = matrix[xy[0] + k1];
								let color;
								if (line[xy[1] + k2] === 1 && !clearLines) { // 矩阵与方块重合
									color = 2;
								} else {
									color = 1;
								}
								line[xy[1] + k2] = color;
								matrix[xy[0] + k1] = line;
							}
						})
					));
					//开启辅助线
					// if (this.help) {
					// 	let shapeWidth = shape[0].length;
					// 	let shapeHeight = shape.length;
					// 	let x = xy[0];
					// 	let y = xy[1];
					// 	let color = 3;
					// 	matrix.forEach(function(m, k1) {
					// 		m.forEach(function(n, k2) {
					// 			if (k2 >= y && k2 < (y + shapeWidth)) {
					// 				if (k1 >= (x + shapeHeight) && n === 0) {
					// 					m[k2] = color;
					// 				}
					// 			}
					// 		})
					// 	})
					// }


				}
				return matrix;
			}
		}
	}
</script>

<style scoped lang="less">
	.matrix {
		border: 2px solid #000;
		padding: 3px 1px 1px 3px;
		width: 228px;

		p {
			width: 220px;
			height: 22px;
		}
	}
</style>
