import constVal from '../../unit/const.js';

let {
	lastRecord
} = constVal;

let initState = lastRecord && !isNaN(parseInt(lastRecord.speedRun, 10)) ?
	parseInt(lastRecord.speedRun, 10) : 1;
if (initState < 1 || initState > 6) {
	initState = 1;
}

export default {
	state: initState,
	speedRun(state, payload) {
		state.speedRun = payload; // 最大分数
	}
};