import constVal from '../../unit/const.js';

let {
	lastRecord,
	maxPoint
} = constVal;


let initState = lastRecord && !isNaN(parseInt(lastRecord.points, 10)) ?
	parseInt(lastRecord.points, 10) : 0;

if (initState < 0) {
	initState = 0;
} else if (initState > maxPoint) {
	initState = maxPoint;
}

export default {
	state: initState,
	points(state, payload) {
		state.points = payload > maxPoint ? maxPoint : payload; // 最大分数
	}
};
